<template>
  <v-row class="ma-sm-4">
    <v-col cols="12">
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title class="py-7">
          <v-row>
            <v-col
              cols="12"
              sm="6"
            >
              Запрещённые слова: 3
            </v-col>
            <v-col class="text-right">
              <v-dialog
                v-model="dialog"
                persistent
                max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    depressed
                    color="primary"
                    class="text-none"
                    large
                    height="36"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Добавить
                  </v-btn>
                </template>

                <v-card>
                  <v-card-title>
                    <span class="text-h5">Добавить слово</span>
                  </v-card-title>

                  <v-card-text>
                    <v-text-field
                      v-model="word"
                      hide-details="auto"
                      label="Слово"
                    />
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer />

                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      Отмена
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      Добавить
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </v-card-title>
        <v-list>
          <template v-for="(item, i) of ['Плохо', 'Ужасно']">
            <v-divider :key="`${i}-divider`" />

            <v-list-item
              :key="`${i}-${item}`"
              class="px-6"
            >
              <v-list-item-content>
                <v-list-item-title>{{ item }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-btn
                  icon
                  color="error"
                >
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>

            <v-divider
              v-if="2 === i + 1"
              :key="`${i}-divider-2`"
            />
          </template>
        </v-list>

        <div class="text-center pt-3">
          <v-pagination
            v-model="page"
            :total-visible="9"
            :length="Math.ceil(3 / itemsPerPage)"
          />

          <v-select
            v-model="itemsPerPage"
            :items="[5, 10, 15]"
            label="Слов на странице"
            class="pa-4 pt-5"
            @change="firstPage"
          />
        </div>
      </v-card>
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <v-btn
        block
        depressed
        class="text-none rounded-lg"
        x-large
      >
        Выключить чат
      </v-btn>
    </v-col>
    <v-col>
      <v-btn
        block
        depressed
        color="primary"
        class="text-none rounded-lg"
        x-large
      >
        Включить чат
      </v-btn>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      word: '',
      dialog: false,
      page: 1,
      itemsPerPage: 5,
    };
  },
  computed: {
    from() {
      return this.itemsPerPage * (this.page - 1);
    },
    to() {
      return this.from + this.itemsPerPage;
    },
  },
  methods: {
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.word = '';
      });
    },
    firstPage() {
      this.page = 1;
    },
  },
};
</script>
